import Vue from "vue";
import App from "./App.vue";
import store from "./store";
import vuetify from "./plugins/vuetify";
import { mapMutations, mapState } from "vuex";
import { get_data } from "./utils/utils";
import VueCodemirror from "vue-codemirror/src";
import "codemirror/lib/codemirror.css";
import "codemirror/theme/material.css";
import "codemirror/mode/css/css";
import "codemirror/mode/nginx/nginx";
import "codemirror/mode/xml/xml"

Vue.config.productionTip = false;

//  当前所支持的弹框布局
Vue.prototype.layouts = [
  {
    type: 1,
    img:
      "https://api.monosnap.com/file/download?id=z3OaPpZlG7zeOjtZeCOjYERr0l2hWz"
  }
];

Vue.filter("keep_pure_number", str => str.replace(/[^0-9\.]/gi, ""));
Vue.filter(
  "replace_point_to_text",
  str => ({ pc: "电脑端", tablet: "平板端", mobile: "手机端" }[str])
);

Vue.use(VueCodemirror, {
  options: {
    theme: "material",
    tabSize: 2,
    lineNumbers: true,
    line: true,
    mode: 'text/css'
  }
});

//  先获取数据
get_data().then(data => {
  new Vue({
    store,
    vuetify,
    render: h => h(App),
    computed: {
      ...mapState(["data"])
    },
    created() {
      this.delete_useless_style();
      this.updateData(data);
    },
    methods: {
      ...mapMutations(["updateData"]),
      /**
       * 删除页面多余的样式表
       */
      delete_useless_style() {
        let delete_style_array = ["forms.min.css", "nav-menus.min.css"];
        $("link").map((key, el) => {
          delete_style_array.map(rule => {
            if (~el.href.indexOf(rule)) {
              $(el).remove();
            }
          });
        });
      }
    }
  }).$mount("#app");
});
