<template>
  <div class="layout1">
    <!--显示条件选择-->
    <div class="config-item area" style="flex-direction: column">
      <v-radio-group
        v-model="currentData.data.condition.type"
        label="显示条件选择"
        @change="condition_change"
      >
        <v-radio
          v-for="(condition, key) in conditions"
          :key="key"
          :label="condition.desc"
          :value="condition.type"
        >
        </v-radio>
      </v-radio-group>

      <template v-if="currentData.data.condition.type === 1">
        <v-subheader class="pl-0">设置显示弹框广告所需的<mark>秒数</mark></v-subheader>
        <v-text-field
          type="number"
          editable
          hint="如希望用户在该页面停留5分钟后显示弹窗广告, 那么因该填写 300 (300 = 5 x 60)"
          v-model="currentData.data.condition.data"
          persistent-hint
          :rules="[rules.minNumber(currentData.data.condition.data, 30)]"
        >
        </v-text-field>
      </template>
      <template v-if="currentData.data.condition.type === 2">
        <v-subheader class="pl-0">拖拽滑杆选择滚动距离<mark>百分比</mark></v-subheader>
        <v-slider
          v-model="currentData.data.condition.data"
          thumb-label="always"
          style="width: 100%"
        >
        </v-slider>
      </template>
    </div>

    <!--再次显示条件选择-->
    <div class="config-item area" style="flex-direction: column">
      <h2>再次显示条件控制</h2>

      <v-subheader class="pl-0">设置重复显示<mark>次数</mark></v-subheader>
      <v-slider
        v-model="currentData.data.conditionForReshow.count"
        thumb-label="always"
        min="0"
        max="3"
        step="1"
        style="width: 100%"
      ></v-slider>
      <v-subheader class="pl-0">设置显示弹框再次显示所需要的<mark>小时数</mark></v-subheader>
      <v-text-field
        type="number"
        editable
        hint="如果希望用户关了广告超过一天后再次显示, 那么此处因该设置为 24"
        v-model="currentData.data.conditionForReshow.data"
        persistent-hint
        :rules="[rules.minNumber(currentData.data.conditionForReshow.data, 3)]"
      >
      </v-text-field>
    </div>

    <!--触发方式-->
    <div class="config-item">
      <v-text-field
        label="触发方式记录"
        outlined
        v-model="currentData.data.type"
      >
      </v-text-field>
      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-icon v-bind="attrs" v-on="on" @click="open_icon_docs">help</v-icon>
        </template>
        <span>
          该值用于设置Massager用户跟踪, 点击查看详情
        </span>
      </v-tooltip>
    </div>

    <!--图片主题色-->
    <div class="config-item area" style="flex-direction: column">
      <v-switch label="是否为亮色背景图" v-model="isLightSwitch"></v-switch>
      <v-subheader class="pl-0">该值将会影响右上角关闭按钮的颜色</v-subheader>
    </div>

    <!--背景图添加-->
    <div class="config-item area" style="flex-direction: column">
      <h2>背景图配置</h2>
      <v-text-field
        label="电脑、平板端背景图"
        hint="该背景图电脑与平板端通用"
        outlined
        v-model="currentData.data.layout.backgrounds.pc"
        style="width: 100%"
      >
      </v-text-field>
      <v-text-field
        label="手机端端背景图"
        hint="该背景图只用于手机端"
        outlined
        v-model="currentData.data.layout.backgrounds.mobile"
        style="width: 100%"
      >
      </v-text-field>
    </div>

    <!--号召语-->
    <div class="config-item area" style="flex-direction: column">
      <h2>号召语</h2>
      <v-subheader class="pl-0">(号召语中可以存在html代码)</v-subheader>
      <codemirror
        v-model="currentData.data.layout.sign.text"
        :options="{
          mode: `text/html`
        }"
        class="codemirror-item"
      ></codemirror>

      <v-subheader class="pl-0">自适应配置</v-subheader>
      <v-row style="width: 100%" class="responsive-box">
        <v-col cols="12">
          <v-row>
            <v-col cols="2">端口</v-col>
            <v-col cols="3">行高</v-col>
            <v-col cols="3">字体大小</v-col>
            <v-col cols="4">颜色</v-col>
          </v-row>
          <template
            v-for="(item, key) in currentData.data.layout.sign.responses"
          >
            <v-row :key="key">
              <v-col cols="2" class="pt-7">{{
                key | replace_point_to_text
              }}</v-col>
              <v-col cols="3">
                <v-text-field outlined v-model="item.lineHeight"></v-text-field>
              </v-col>
              <v-col cols="3">
                <v-text-field outlined v-model="item.fontSize"></v-text-field>
              </v-col>
              <v-col cols="4">
                <v-text-field outlined v-model="item.color"></v-text-field>
              </v-col>
            </v-row>
          </template>
        </v-col>
      </v-row>
    </div>

    <!--buttons-->
    <div class="config-item area" style="flex-direction: column">
      <buttons />
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import buttons from "../components/buttons/buttons";

export default {
  name: "layout1",
  computed: {
    ...mapState(["currentData"]),
    isLightSwitch: {
      get() {
        return this.currentData.data.layout.isLight;
      },
      set(value) {
        this.currentData.data.layout.isLight = value;
      }
    }
  },
  components: {
    buttons
  },
  data() {
    this.conditions = [
      { type: 1, desc: "按时间显示" },
      { type: 2, desc: "按滚动距离显示" }
    ];
    this.rules = {
      minNumber(current, number) {
        return current >= number ? true : `该值必须要大于${number}`;
      }
    };
    return {
      condition2Range: 0
    };
  },
  mounted() {},
  methods: {
    condition_change(value) {
      //  为什么能直接赋值? object机制
      this.currentData.data.condition.type = value;
    },
    open_icon_docs() {
      window.open(
        "https://docs.google.com/document/d/1H3j5VcIwKesqOO9hnBQ_MO2KrAq_03qh9fwQoGP-t6g/edit#bookmark=id.cbue15o9ngah"
      );
    }
  }
};
</script>

