<template>
  <section class="campaign-buttons">
    <v-list width="100%">
      <v-subheader>按钮配置</v-subheader>
      <draggable
        v-model="buttonsData"
        draggable=".button-item"
        handle=".drag-button"
      >
        <div class="button-item" v-for="(item, key) in buttonsData">
          <v-list-item :key="key">
            <v-list-item-icon>
              <v-btn icon class="drag-button">
                <v-icon>drag_handle</v-icon>
              </v-btn>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{ item.text }}</v-list-item-title>
            </v-list-item-content>
            <v-list-item-icon>
              <v-btn icon color="primary" @click="on_edit(item.order)"
                ><v-icon>edit</v-icon></v-btn
              >
              <v-btn icon>
                <v-icon color="#f20" @click="on_remove(item.order)"
                  >remove</v-icon
                >
              </v-btn>
            </v-list-item-icon>
          </v-list-item>
          <v-divider></v-divider>
        </div>
      </draggable>
      <v-row class="d-flex flex-row justify-end">
        <v-btn class="mt-6 mr-6" color="primary" @click="add_one">
          <v-icon>add</v-icon>
          添加新按钮
        </v-btn>
      </v-row>
    </v-list>

    <!--dialog-->
    <v-dialog width="70vw" v-model="dialogShow">
      <v-card>
        <v-card-title class="headline grey lighten-2"
          >{{ isAddButton ? "新增按钮" : "修改按钮" }}
          <v-btn icon
            ><v-icon @click="go_docs">help</v-icon></v-btn
          ></v-card-title
        >

        <v-card-text class="mt-5">
          <v-row>
            <v-col cols="5">
              <v-text-field
                label="图标链接"
                outlined
                v-model="buttonData.icon"
                hint="该图标链接将会显示在按钮的左侧"
              >
              </v-text-field>
              <v-text-field
                label="按钮号召语"
                outlined
                v-model="buttonData.text"
                hint="该号召语将会与上面的图标一并显示"
              >
              </v-text-field>
              <v-text-field
                label="跳转链接"
                outlined
                v-model="buttonData.link"
                hint="当用户点击按钮是, 所需要跳转的链接"
              >
              </v-text-field>
            </v-col>
            <v-col cols="3">
              <v-text-field
                label="按钮文字颜色"
                outlined
                v-model="buttonData.color"
                hint="按钮内部文字的颜色"
              >
              </v-text-field>
              <v-text-field
                label="按钮颜色"
                outlined
                v-model="buttonData.backgroundColor"
                hint="该颜色为默认用户看到该按钮的颜色"
              >
              </v-text-field>
              <v-text-field
                label="hover颜色"
                outlined
                v-model="buttonData.backgroundColorHover"
                hint="当用户鼠标移入按钮时, 按钮所变化的颜色"
              >
              </v-text-field>
<!--              <v-text-field-->
<!--                label="阴影设置"-->
<!--                outlined-->
<!--                v-model="buttonData.boxShadow"-->
<!--                hint="当用户鼠标移入按钮时, 所生成的阴影属性"-->
<!--              >-->
<!--              </v-text-field>-->
            </v-col>
            <v-col cols="4">
              <v-row style="width: 100%">
                <v-col cols="12">
                  <v-row>
                    <v-col cols="3">端口</v-col>
                    <v-col cols="4">行高</v-col>
                    <v-col cols="4">字体大小</v-col>
                  </v-row>
                  <template v-for="(item, key) in buttonData.responses">
                    <v-row :key="key">
                      <v-col cols="3" class="pt-8">{{
                        key | replace_point_to_text
                      }}</v-col>
                      <v-col cols="4">
                        <v-text-field
                          outlined
                          v-model="item.lineHeight"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="4">
                        <v-text-field
                          outlined
                          v-model="item.fontSize"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </template>
                </v-col>
              </v-row>
              <v-switch
                label="是否提示隐私权政策"
                v-model="buttonData.isPrivacy"
                class="switch-control"
              ></v-switch>
            </v-col>
          </v-row>
        </v-card-text>

        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" @click="dialogShow = false">取消</v-btn>
          <v-btn color="primary" @click="save_button">{{
            isAddButton ? "添加" : "保存"
          }}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </section>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import draggable from "vuedraggable";

let buttonTemplate = {
  icon: "",
  text: "",
  link: "",
  isPrivacy: true,
  color: "",
  backgroundColor: "",
  backgroundColorHover: "",
  boxShadow: "",
  responses: {
    pc: { lineHeight: "1.3em", fontSize: "18px" },
    tablet: { lineHeight: "1.3em", fontSize: "16px" }
  }
};

export default {
  name: "buttons",
  computed: {
    ...mapState(["currentData"]),
    buttonsData: {
      get() {
        return this.currentData.data.layout.buttons;
      },
      set(value) {
        let i = 0;
        value.map(item => (item.order = i++));
        this.currentData.data.layout.buttons = value;
      }
    }
  },
  components: {
    draggable
  },
  data() {
    return {
      buttonData: {},
      dialogShow: false,
      isAddButton: false
    };
  },
  mounted() {},
  methods: {
    //  添加button点击时
    add_one() {
      this.dialogShow = true;
      this.isAddButton = true;
      this.buttonData = Object.assign({}, buttonTemplate);
    },
    //  修改button点击时
    on_edit(order) {
      this.dialogShow = true;
      this.isAddButton = false;
      //  根据order找到对应的button配置
      this.buttonData = this.currentData.data.layout.buttons.find(
        item => item.order === order
      );
    },
    //  删除按钮点击时候
    on_remove(order) {
      let currentItem = this.currentData.data.layout.buttons.find(
        item => item.order === order
      );
      if (confirm("确定删除吗?")) {
        this.currentData.data.layout.buttons.splice(
          this.currentData.data.layout.buttons.indexOf(currentItem),
          1
        );
      }
    },
    save_button() {
      //  如果是添加操作
      if (this.isAddButton === true) {
        this.currentData.data.layout.buttons.push(this.buttonData);
        this.dialogShow = false;
      } else {
        //  修改操作
        this.dialogShow = false;
      }
    },
    go_docs() {
      window.open("https://docs.google.com/document/d/1H3j5VcIwKesqOO9hnBQ_MO2KrAq_03qh9fwQoGP-t6g/edit#heading=h.bi3jrpi2r3ks");
    }
  }
};
</script>

<style scoped lang="scss">
.campaign-buttons {
  width: 100%;
}
.switch-control {
  margin-top: -16px;
}
</style>
