import { darken } from "vuetify/lib/services/theme/utils";

const get_data = () => {
  return new Promise(res => {
    if (is_dev()) {
      $.get(`http://localhost:8907/data`).then(data => {
        res(data);
      });
    } else {
      $.ajax({
        type: "POST",
        url: ajaxurl,
        data: {
          action: "Modules_campaign_generator_Ajax",
          function: "ajax_get_data"
        },
        dataType: "json",
        cache: false
      }).then(data => {
        res(data);
      });
    }
  });
};

const save_data = data => {
  return new Promise(res => {
    if (is_dev()) {
      res();
    } else {
      $.ajax({
        type: "POST",
        url: ajaxurl,
        data: {
          action: "Modules_campaign_generator_Ajax",
          function: "ajax_save_data",
          reData: {
            paras: data
          }
        },
        dataType: "json",
        cache: false
      }).then(() => res());
    }
  });
};

const is_dev = () => {
  return !!~window.location.href.indexOf(`localhost:80`);
};

/**
 * hex to rgba
 * @param {string} hex
 * @returns {string}
 */
const hex_to_rgb = hex => {
  let shorthandRegex = /^#?([a-f\d])([a-f\d])([a-f\d])$/i;
  hex = hex.replace(shorthandRegex, function(m, r, g, b) {
    return r + r + g + g + b + b;
  });

  let result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
  return result
    ? `${parseInt(result[1], 16)},${parseInt(result[2], 16)},${parseInt(
        result[3],
        16
      )}`
    : "";
};

export { get_data, save_data, hex_to_rgb };
