import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    //  用于储存所有数据
    data: {},
    //  用于储存当前数据
    currentData: {},
    //  用于储存当前数据id
    currentId: 0
  },
  mutations: {
    updateData(state, data) {
      state.data = data;
    },
    updateCurrentData(state, data) {
      state.data = data;
    },
    updateCurrentId(state, id) {
      state.currentId = id;
      state.data.map(item => {
        if (item.id === id) {
          state.currentData = item;
        }
      });
    }
  },
  actions: {},
  modules: {}
});
