<template>
  <v-app>
    <v-main>
      <v-container>
        <v-row align="start">
          <v-col cols="1">
            <h2>弹框广告配置</h2>
          </v-col>
          <v-col cols="2">
            <v-select
              :value="currentId"
              :items="data"
              :item-text="set_select_title"
              :item-value="set_select_value"
              label="弹框选择"
              @change="select_change"
            >
            </v-select>
          </v-col>
          <v-col cols="6">
            <v-btn color="primary" class="mr-4" @click="add_new_campaign">
              <v-icon>add</v-icon>
              新增弹框
            </v-btn>
            <v-btn color="#C5E1A5" class="mr-4" @click="add_copy_campaign">
              <v-icon>content_copy</v-icon>
              复制该弹框
            </v-btn>
            <v-btn color="success" class="mr-4" @click="save_data">
              <template v-if="saving">
                <v-progress-circular
                  :size="24"
                  color="#fff"
                  :width="2"
                  indeterminate
                ></v-progress-circular>
                <span class="pl-4">保存中....</span>
              </template>
              <template v-else>
                <v-icon>save</v-icon>
                保存数据
              </template>
            </v-btn>

            <v-btn class="mr-4" @click="open_video">
              <v-icon>mdi-youtube</v-icon>
              视频教程
            </v-btn>

            <v-btn class="mr-4" @click="open_doc">
              <v-icon>mdi-file-document</v-icon>
              使用说明
            </v-btn>
          </v-col>
        </v-row>
        <v-row class="config-box">
          <v-col cols="12">
            <v-btn color="primary" v-if="isHide" @click="isHide = false">
              <v-icon>arrow_forward</v-icon>
              展开
            </v-btn>
            <v-btn color="primary" v-else @click="isHide = true">
              <v-icon>arrow_back</v-icon>
              收起
            </v-btn>
          </v-col>
          <v-col cols="4" v-show="!isHide" id="config-area">
            <!--弹框名称-->
            <div class="config-item">
              <v-text-field
                label="弹框名称"
                outlined
                v-model="currentData.name"
                hint="弹框名称用于顶部是弹框选择, 用于区分不同弹框方便站长切换,用户是看不到这个名字的"
              >
              </v-text-field>
            </div>

            <!--是否启用该弹框-->
            <div class="config-item area flex-column">
              <v-switch
                label="是否启用该弹框"
                v-model="currentData.isOpen"
              ></v-switch>
            </div>

            <!--白名单-->
            <div class="config-item area flex-column">
              <div class="d-flex">
                <h2>白名单列表</h2>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" @click="open_docs">
                      help
                    </v-icon>
                  </template>
                </v-tooltip>
              </div>

              <codemirror
                v-model="currentData.whitelist"
                :options="codemirrorOptions"
                class="codemirror-item"
              ></codemirror>

              <div class="d-flex">
                <h2>黑名单列表</h2>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" @click="open_docs">
                      help
                    </v-icon>
                  </template>
                </v-tooltip>
              </div>

              <codemirror
                v-model="currentData.blacklist"
                :options="codemirrorOptions"
                class="codemirror-item"
              ></codemirror>
            </div>

            <!--布局选择-->
            <div class="config-item">
              <v-radio-group
                v-model="currentData.data.layout.type"
                label="布局选择"
              >
                <v-radio
                  v-for="(layout, key) in layouts"
                  :key="key"
                  :label="`布局 ${layout.type}`"
                  :value="layout.type"
                >
                  <template slot="label">
                    布局 {{ layout.type }}
                    <img :src="layout.img" class="layout-campaign" />
                  </template>
                </v-radio>
              </v-radio-group>
            </div>

            <template v-if="currentData.data.layout.type === 1">
              <layout1 />
            </template>

            <div class="config-item area flex-column">
              <h2>自定义样式</h2>
              <codemirror
                v-model="currentData.data.customStyle"
                style="width: 100%"
              ></codemirror>
            </div>

            <div class="config-item area" style="flex-direction: column">
              <h2>隐私政策配置</h2>
              <v-subheader v-show="currentId !== 0" style="color: #f20"
                >隐私权为公用数据, 若要调整请到第一个弹框中进行设置
              </v-subheader>
              <div style="width: 100%;">
                <v-text-field
                  label="标题"
                  outlined
                  v-model="privacyData.title"
                  :disabled="currentId !== 0"
                  hint="对应用户看到隐私权弹框的标题"
                >
                </v-text-field>
                <v-text-field
                  label="描述"
                  outlined
                  :disabled="currentId !== 0"
                  v-model="privacyData.describe"
                  hint="对应用户看到的隐私权描述信息, 请用**符号, 将需要添加链接的文本分开, 如 **阅读隐私权政策**"
                >
                </v-text-field>
                <v-text-field
                  label="链接"
                  outlined
                  :disabled="currentId !== 0"
                  v-model="privacyData.link"
                  hint="隐私权政策跳转链接"
                >
                </v-text-field>
                <v-text-field
                  label="取消按钮翻译"
                  outlined
                  :disabled="currentId !== 0"
                  v-model="privacyData.cancel"
                >
                </v-text-field>
                <v-text-field
                  label="同意按钮翻译"
                  outlined
                  :disabled="currentId !== 0"
                  v-model="privacyData.agree"
                >
                </v-text-field>
              </div>
            </div>

            <div class="d-flex flex-row justify-end">
              <v-btn color="error" @click="remove_campaign">
                <v-icon>remove</v-icon>
                删除
              </v-btn>
            </div>
          </v-col>
          <v-col :cols="isHide ? 12 : 8">
            <preview />
          </v-col>
        </v-row>
        <v-alert v-if="isShowAlert" :type="alertType" class="campaign-alert">
          {{ alertText }}
        </v-alert>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import layout1 from "./layouts/layout1";
import preview from "./components/preview";
import { save_data } from "./utils/utils";

let alertTimer = null;

export default {
  name: "App",
  computed: {
    ...mapState(["data", "currentId", "currentData"])
  },
  components: {
    layout1,
    preview
  },
  data() {
    this.codemirrorOptions = {
      mode: "text"
    };
    return {
      isHide: false,
      privacyData: {
        title: "聊天前请阅读并同意我们的隐私权政策.",
        describe: "你已阅读并同意我们的**隐私权政策**吗?",
        link: "https://tr.kingdomsalvation.org/privacy-policy.html",
        cancel: "取消",
        agree: "确定"
      },
      isShowAlert: false,
      alertType: "success",
      alertText: "",
      saving: false
    };
  },
  created() {
    //  默认选择第一个
    this.select_change(0);
  },
  mounted() {
    if (this.currentData.data.privacyData && this.currentData.data.privacyData.title) {
      this.privacyData = this.currentData.data.privacyData;
    }
  },
  methods: {
    ...mapMutations(["updateCurrentId"]),
    open_video() {
      window.open("https://youtu.be/zLysdYqpZ4o");
    },
    set_select_title(item) {
      return `${item.name}`;
    },
    set_select_value(item) {
      return item.id;
    },
    select_change(value) {
      this.updateCurrentId(value);
    },
    //  使用说明文档
    open_doc() {
      window.open(
        "https://docs.google.com/document/d/1H3j5VcIwKesqOO9hnBQ_MO2KrAq_03qh9fwQoGP-t6g/edit#"
      );
    },
    //  白名单与黑名单说明
    open_docs() {
      window.open(
        "https://docs.google.com/document/d/1H3j5VcIwKesqOO9hnBQ_MO2KrAq_03qh9fwQoGP-t6g/edit#heading=h.f2wa5n7iluvi"
      );
    },
    //  添加新弹框
    add_new_campaign() {
      $.get(
        `https://centereu.kingdomsalvation.org//cdn/v3/vendor/campaign/campaign-panel/layouts/layout1_default.json?v=${Date.now()}`
      ).then(data => {
        let newConfig = Object.assign({}, data[0]);
        newConfig.name = "新增弹窗";
        newConfig.id = Math.ceil(Math.random() * 100000000);
        this.data.push(newConfig);
        this.updateCurrentId(newConfig.id);
        this.show_alert("success", "成功添加新的弹框配置, 请注意查看");
      });
    },
    //  复制弹框
    add_copy_campaign() {
      let newConfig = JSON.parse(JSON.stringify(this.currentData));
      newConfig.id = Math.ceil(Math.random() * 100000000);
      newConfig.name = `${newConfig.name} - copy`;
      this.data.push(newConfig);
      this.updateCurrentId(newConfig.id);
      this.show_alert("success", "复制新的弹框配置成功, 请注意查看");
    },
    /**
     * 保存数据
     */
    save_data(ev, skip = false) {
      if (!skip) {
        if (this.saving) return;

        this.saving = true;
      }

      let newJsonString = JSON.stringify(this.data);
      let newObj = JSON.parse(newJsonString);

      //  给所有的配置, 添加 privacyData字段
      newObj.map(item => {
        //  判断显示条件与再次显示条件控制是否符合最小值
        item.data.conditionForReshow.data = Math.max(
          3,
          item.data.conditionForReshow.data
        );

        //  判断显示条件type为1时, 最小秒数是否大于30
        if (item.data.condition.type === 1) {
          item.data.condition.data = Math.max(30, item.data.condition.data);
        }

        item.data.privacyData = this.privacyData;
      });

      save_data(encodeURIComponent(JSON.stringify(newObj))).then(() => {
        if (!skip) {
          this.saving = false;
          this.show_alert("success", "数据保存成功!");
        }
      });
    },
    //  显示提示框
    show_alert(type, text) {
      window.clearTimeout(alertTimer);
      this.isShowAlert = true;
      this.alertType = type;
      this.alertText = text;

      alertTimer = setTimeout(() => {
        this.isShowAlert = false;
      }, 3000);
    },
    /**
     * 删除一个弹框
     */
    remove_campaign() {
      if (this.data.length === 1) {
        this.show_alert("error", "无法删除, 必须存在一个弹框配置!");
        return;
      }

      if (this.currentData.id === 0) {
        this.show_alert("error", "无法删除, 全站弹框配置!");
        return;
      }

      if (confirm("确定删除吗?")) {
        let current = this.data.find(item => item.id === this.currentData.id);
        this.data.splice(this.data.indexOf(current));
        this.show_alert("success", "刪除成功");
        this.updateCurrentId(this.data[0].id);
        this.save_data(null, true);
      }
    }
  }
};
</script>

