<template>
  <section
    :class="{
      'preview-box': true,
      'tablet-campaign-wrap': toggle_exclusive === 1 || toggle_exclusive === 2,
      'mobile-campaign-wrap': toggle_exclusive === 2
    }"
  >
    <div class="scale-box" style="width: 200px">
      <h2>
        预览缩放
        <mark>{{ scale }}%</mark>
      </h2>
      <v-slider
        v-model="scale"
        :thumb-label="true"
        style="width: 100px"
        min="30"
        max="100"
      ></v-slider>
    </div>

    <div class="device-change-box">
      <v-btn-toggle v-model="toggle_exclusive">
        <v-btn>电脑端 </v-btn>

        <v-btn>平板端</v-btn>

        <v-btn>手机端</v-btn>
      </v-btn-toggle>
    </div>

    <div id="campaignStyle" ref="campaignStyle">
      {{ JSON.stringify(currentData.data) }}
    </div>
    <section
      :class="{
        'campaign-layout-1': true,
        'campaign-modal': true,
        light: currentData.data.layout.isLight,
        dark: !currentData.data.layout.isLight
      }"
      id="campaign-layout-1"
      :style="{
        transform: `scale(${scale / 100})`
      }"
    >
      <div class="right-box">
        <p class="campaign-layout-1-sign" id="sign-9613130" v-html="currentData.data.layout.sign.text">
        </p>
        <div class="buttons-area">
          <button
            v-for="(item, key) in currentData.data.layout.buttons"
            :key="key"
            class="campaign-button"
            data-link="https://m.me/tl.kingdomsalvation?a=b&amp;ref=1"
            :id="`button-${key}`"
            data-is-privacy="true"
          >
            <img v-if="item.icon.length > 0" :src="item.icon" />
            {{ item.text }}
          </button>
        </div>
      </div>
      <button id="campaign-layout-1-close-btn"></button>
    </section>
  </section>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import {hex_to_rgb} from "../utils/utils";

let lastDataString = "";
let styleEl = document.createElement("style");
styleEl.id = "componentsStyleInject";
document.querySelector("head").append(styleEl);

export default {
  computed: {
    ...mapState(["currentData"])
  },
  name: "preview",
  data() {
    return {
      toggle_exclusive: 0,
      scale: 100
    };
  },
  mounted() {
    if (document.querySelector("#campaignStyleInject")) {
      return;
    }
    //  实现实时更新
    setInterval(() => {
      if (this.$refs) {
        if (lastDataString !== this.$refs.campaignStyle.innerHTML) {
          lastDataString = this.$refs.campaignStyle.innerHTML;
          this.update_style();
        }
      }
    }, 300);
  },
  methods: {
    //  更新样式
    update_style() {
      let style = "";

      //  添加号召语样式
      style += this.generator_responsive_style(
        "sign-9613130",
        this.currentData.data.layout.sign.responses
      );

      //  处理按钮渲染
      Object.values(this.currentData.data.layout.buttons).map((item, key) => {
        style += this.generator_responsive_style(
          `button-${key}`,
          item.responses
        );
        style += this.generator_style(`button-${key}`, {
          backgroundColor: item.backgroundColor,
          color: item.color,
          hover: {
            backgroundColor: item.backgroundColorHover,
            boxShadow: `0 5px 10px rgba(${hex_to_rgb(item.backgroundColor)},.2)`
          }
        });
      });

      //  处理背景图渲染
      let newResponsiveObj = {};
      Object.keys(this.currentData.data.layout.backgrounds).map(
        key =>
          (newResponsiveObj[key] = {
            backgroundImage: `url("${this.currentData.data.layout.backgrounds[key]}")`
          })
      );
      style += this.generator_responsive_style(
        "campaign-layout-1",
        newResponsiveObj
      );

      //  添加自定义样式
      style += this.currentData.data.customStyle

      styleEl.innerHTML = style;
    },
    format_style_obj(styleObj) {
      let $div = $("<div />").css(styleObj);
      return $div.attr("style");
    },
    generator_responsive_style(id, styleObj) {
      let style = ``;
      Object.keys(styleObj).map(key => {
        switch (key) {
          case "mobile":
            style += `.mobile-campaign-wrap #${id}{${this.format_style_obj(
              styleObj[key]
            )}}`;
            break;
          case "tablet":
            style += `.tablet-campaign-wrap #${id}{${this.format_style_obj(
              styleObj[key]
            )}}`;
            break;
          case "pc":
            style += `#${id}{${this.format_style_obj(styleObj[key])}}`;
            break;
        }
      });
      return style;
    },
    generator_style(id, styleObj) {
      let style = "";
      style = `#${id}{${this.format_style_obj(styleObj)}}`;

      if (styleObj["hover"]) {
        style += `#${id}:hover{${this.format_style_obj(styleObj["hover"])}}`;
      }
      if (styleObj["active"]) {
        style += `#${id}:active{${this.format_style_obj(styleObj["active"])}}`;
      }
      return style;
    }
  }
};
</script>

<style lang="scss">
.preview-box {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  background: #000;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);

  .scale-box {
    position: absolute;
    right: 0;
    top: -90px;
  }
  .device-change-box {
    position: absolute;
    left: 200px;
    top: -60px;
  }
}

#campaign-layout-1 {
  flex-shrink: 0;
  position: relative !important;
  left: 0 !important;
  top: 0 !important;
  opacity: 1;
  z-index: 1;
  margin-left: 16px;
  margin-top: 16px;
  transform-origin: left top;
}

#campaignStyle {
  opacity: 0;
  pointer-events: none;
  display: none;
}

.tablet-campaign-wrap .campaign-layout-1 {
  width: 614px !important;
  height: 360px !important;
}

.mobile-campaign-wrap .campaign-layout-1 {
  width: 320px !important;
  height:  auto !important;
  .right-box {
    margin-top: 200px !important;
  }
}
</style>
